.how-we-working {
    margin-bottom: 199px;
    .container {
        padding: 0;        
    }
    &-title {
        font-family: $font-primary;
        font-weight: 600;
        line-height: 98px;
        color: #3C4D69;
        padding: 181px 0 45px;
        margin: 0;
    }
    .row {
        margin: 0;
    }
    .col-lg-3 {
        padding: 0;
        margin-top: 30px;
    }
    &-block {
        width: 334px;
        height: 542px;
        border: 1px solid #EBEFF0;
        padding: 43px 40px;
        &-text{
            font-family: $font-primary;
            font-size: 18px;
            font-weight: 500;
            line-height: 25.5px;
            color: #707C88;
            padding-bottom: 51px;
            &.step {
                position: relative;
                &::before{
                    content: "";
                    background: url(assets/img/how-we-working-icon-step.svg) center no-repeat;
                    width: 100px;
                    height: 9px;
                    position: absolute;
                    top: 0;
                    right: 0;
                }
            }            
        }
        &-img{
            margin-bottom:35px;
            background-repeat: no-repeat;
            background-image: url(assets/img/sprite-all.png);
            &.icon-one {
                width: 80px;
                height: 80px;
                background-position: 0px -474px;
            }
            &.icon-two{
                width: 60px;
                height: 80px;
                background-position: -102px -474px;
            }
            &.icon-three{
                width: 80px;
                height: 80px;
                background-position: -194px -476px;
            }
            &.icon-four{
                width: 80px;
                height: 80px;
                background-position: -277px -474px;
            }
            &.icon-five{
                width: 80px;
                height: 80px;
                background-position: -379px -474px;
            }
            &.icon-six{
                width: 91px;
                height: 80px;
                background-position: -484px -474px;
            }
            &.icon-seven{
                width: 80px;
                height: 80px;
                background-position: -599px -474px;
            }
            &.icon-eigxt{
                width: 80px;
                height: 80px;
                background-position: -697px -474px;
            }
        }
        &-title{
            font-family: $font-primary;
            font-weight: 600;
            line-height: 29px;
            color: #3C4D69;
            margin-bottom: 34px;
            height: 87px;
        }
        &-description{
            font-family: $font-primary;
            font-weight: 400;
            font-size: 18px;
            line-height: 23px;
            color: #707C88;
            margin-bottom: 34px;
            position: relative;
            &.question-step-one::after{
                content: "";
                width: 18px;
                height: 18px;
                background:url(assets/img/icon-question.png) center no-repeat;
                position: absolute;
                bottom: 20px;
                right: 10px;
            }
            &.question-step-two::after{
                content: "";
                width: 18px;
                height: 18px;
                background:url(assets/img/icon-question.png) center no-repeat;
                position: absolute;
                bottom: 20px;
                right: 33px;
            }
            &.question-step-seven::after{
                content: "";
                width: 18px;
                height: 18px;
                background:url(assets/img/icon-question.png) center no-repeat;
                position: absolute;
                bottom: -4px;
                right: 113px;
            }
        }
        &-other{
            font-family: $font-primary;
            font-weight: 600;
            font-size: 20px;
            line-height: 22px;
            color: #EA142E;
            margin-top: 24px;
        }
        
    }
}