/*8*/
input[type="checkbox"] {
    + label[for="checkbox"] {
        font-size: 20px;
    }
}

input[type="checkbox"],
input[type="radio"] {
    display: none;

    &+label {
        color: #000;
        cursor: pointer;
        font-size: 20px;
        line-height: 1.5; 
        font-family: $font-secondary;
        text-transform: uppercase;

        &:hover {
            color: #000;
        }

        &:before {
            content: '';
            padding: 9px;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 20px;
            margin-bottom: 13px;
            width: 40px;
            height: 40px;
            background-color: #ffffff;
            border: 1px solid #CDCDCD;
        }
    }
}

input[type="checkbox"]:checked + label:before {
    background: url("../assets/img/checkbox.svg") center no-repeat;
}

input[type=radio]+label:before {
    content: '';
    padding: 9px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
    margin-bottom: 13px;
    width: 40px;
    height: 40px;     
    border-radius: 50%;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #CDCDCD;
}

input[type=radio]:checked+label:before {
    content: '';
    background: url("../assets/img/radiobutton.svg") center/18px no-repeat;
}

/*9-11*/
.btn {
    display: inline-block;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    //width: 458px;
    //height: 88px;
    font-size: 16px;
    font-family: $font-secondary;
    font-weight: 700;
    line-height: 1;
    padding: 34px 38px;
    text-transform: uppercase;
    border-radius: 0;
    margin: 2px;

    &:focus {
    color: #ffffff4d;
    background-color: $primary-color-hover;
    border: 1px solid transparent;
    }

}

.btn-primary {
    color: #fff;
    background-color: $secondary-color;
    border: 1px solid transparent;
    box-shadow: 0px 20px 20px 0px rgba(234, 20, 46, 0.2);

    &:hover {
        color: #fff;
        background-color: $secondary-color;
        border-color: $secondary-color;
        text-decoration: none;
        box-shadow: 0px 20px 20px 0px rgba(234, 20, 46, 0.2);
    }
    &:focus {
    color: #ffffff4d;
    background-color: $secondary-color;
    box-shadow: 0px 30px 30px 0px rgba(234, 20, 46, 0.2);

    } 

    &:active:focus {
        box-shadow: 0px 30px 30px 0px rgba(234, 20, 46, 0.2);
    }
}


.btn-secondary {
    color: #fff;
    background: linear-gradient(to left, #EA142E, #212C3F 31.74%);

    &:hover {
        background: linear-gradient(to left, #EA142E, #212C3F 31.74%);
        border-color:#212C3F;

    }
    &:focus {
        color: #ffffff4d;
        background: linear-gradient(to left, #EA142E, #212C3F 31.74%);
        border-color:#212C3F;
        box-shadow: none;
        } 
    
        &:active:focus {
            box-shadow: none;
        }
}


