/*range*/
.range {
    display: flex;
    flex-direction: column;
    .text {
        font-size: 20px;
        line-height: 24px;
        font-family: $font-primary;
        font-weight: 600;
        color: #3C4D69;
        width: 100%;
        max-width: 178px;
        min-width: 92px;
        border: 1px solid #CED6DA;
        padding: 12px 17px;
        text-align: center;
        margin-bottom: 36px;
        span {
            margin-left: 8px;
        }
    }
    input[type=range] {
        -webkit-appearance: none;
        margin-bottom: 20px;
        width: 265px;
        height: 2px;
        //height: 0;
        //border: 1px solid #CED6DA;
        background: #CED6DA;
        background-image: linear-gradient(#FF3334, #FF3334);
        background-size: 15% 100%;
        background-repeat: no-repeat;
        &:focus-visible{
            outline: none;

        }
    }
    input[type=range]::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: #FF3334;
        cursor: pointer; 
        transition: background .3s ease-in-out;
    }   
    input[type=range]::-webkit-slider-runnable-track  {
        -webkit-appearance: none;
        box-shadow: none;
        border: none;
        background: transparent;
    } 
}


