.popovers {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #EA142E;
    border: none;
    box-shadow: 0px 0px 0px 20px #ea142e33;
    padding: 0;
    position: relative;
    span {
        width: 20px;
        height: 2px;
        display: block;
        position: absolute;
        top: 25px;
        left: 15px;
        background-color: #fff;
        &:nth-child(2) {
            transform: rotate(90deg) translate(0px, 0px);
        }             
    } 
    &:focus{
        background: #EA142E;
        border: none;
        span {
            transform: rotate(45deg) translate(0px, 0px);
            &:nth-child(2) {
                transform: rotate(-45deg) translate(0px, 0px);
            } 
        } 
    }
}
/*accordion*/
.accordion{
    width: 100%;
    .accordion-item{
        border-radius: 0;
        border-top: 1px solid #E3E3E3;
        border-top: 1px solid #E3E3E3;
        .accordion-header{
            .accordion-button{
                font-family: $font-primary;
                font-weight: 500;
                line-height: 33px;
                color: #3C4D69;
                padding: 27px 0;
                &::after {
                    content: "";
                    background-image: none;
                }
                span{
                    width: 20px;
                    height: 1px;
                    display: block;
                    position: absolute;
                    top: 42px;
                    right: 10px;
                    background-color: #3C4D69;    
                    &:nth-child(2) {
                        transform: rotate(90deg) translate(0px, 0px);
                    } 
                }      
                &:not(.collapsed) {
                    color: #EA142E;
                    background-color: #fff;
                    box-shadow: none;
                    padding: 27px 0 22px;   
                    span {
                        top: 42px;
                        right: 10px;
                        background: #FFFFFF;
                        transform: rotate(45deg) translate(0px, 0px);
                        &:nth-child(2) {
                            transform: rotate(-45deg) translate(0px, 0px);
                        } 
                        z-index: 1;
                    } 
                    &::after {
                        content: "";
                        width: 40px;
                        height: 40px;
                        background: #EA142E;  
                        border-radius: 50%;
                        position: absolute;
                        top: 22px;
                        right: 0px;
                    }                                           
                }
                &:focus{
                    border: none;
                    border-color: #fff;
                    outline: 0;
                    box-shadow: none;  
                }
            }
        }
        .accordion-body{
            padding: 0 0 30px 0;
            strong{
                font-family: $font-primary;
                font-weight: 400;
                font-size: 18px;
                line-height: 25px;
                color: #707C88;

            }
            
        }
    }
}


