.comparison {
    width: 100%;    
    position: relative;
    margin-bottom: 204px;
    &::after {
        content: "";
        background: url(assets/img/tel-backgraund-after.png) center no-repeat;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 172px;
        z-index: -1;
    } 
    .container{
        padding: 0;
    }
    &-title{
        font-weight: 600;
        line-height: 98px;
        color: #3C4D69;
        padding-bottom: 95px;
        margin: 0;
    }
    &-table{
        display: flex;
        margin-bottom: 200px;
        &-colomn{
            flex-direction: column;
            &-name{
                font-weight: 500;
                line-height: 31px;
                color: #3C4D69;
                padding: 25px 0;
                width: 276px;
                margin-bottom: 0; 
                margin-right: 41px; 
                &:first-child{
                    margin-top: 71px;
                    border-top: 1px solid #E3E3E3;     
                    border-bottom: 1px solid #E3E3E3;     
                }        
                &:nth-child(2), 
                &:nth-child(4){
                    background: #F9F9F9;
                }
                &:nth-child(4){
                    margin-bottom: 99px;
                }
                &:nth-child(5),
                &:nth-child(6){
                    border-top: 1px solid #E3E3E3; 
                }
                    &:nth-child(6){
                    border-bottom: 1px solid #E3E3E3;     
                }
            }
            &-flat{
                width: 536px;
                margin-right: 41px;
                &-title{
                    font-weight: 600;
                    line-height: 31px;
                    color: #3C4D69;
                    padding: 0 40px 40px;
                    margin: 0;       
                    border-bottom: 1px solid #E3E3E3;     
                }
                &-description{
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 23px;
                    color: #3C4D69;
                    padding: 30px 218px 30px 36px;
                    &:nth-child(3), 
                    &:nth-child(5),
                    &:nth-child(7){
                        background: #F9F9F9;
                    }
                    &:nth-child(4), 
                    &:nth-child(5){
                        padding: 17px 218px 17px 36px;;
                    }    
                    &:nth-child(5){
                        margin-bottom: 99px;
                    }                          
                }
            }
            &-invest{
                width: 536px;
                &-title{
                    font-weight: 600;
                    line-height: 31px;
                    color: #EA142E;
                    padding: 0 40px 40px;
                    margin: 0;     
                    border-bottom: 1px solid #E3E3E3; 
                }
                &-description{
                    font-weight: 800;
                    font-size: 18px;
                    line-height: 23px;
                    color: #3C4D69;
                    padding: 30px 218px 30px 36px;
                    &:nth-child(2),
                    &:nth-child(6),
                    &:nth-child(7){
                        font-weight: 400;
                    }  
                    &:nth-child(4){
                        padding-right: 270px;
                    }
                    &:nth-child(3), 
                    &:nth-child(5),
                    &:nth-child(7){
                        background: #FEF3F5;
                    }      
                    &:nth-child(4), 
                    &:nth-child(5){
                        padding: 17px 218px 17px 36px;;
                    }  
                    &:nth-child(5){
                        margin-bottom: 99px;
                    }                  
                }
            }   
        }
    }
    .tel-block{
        display: flex;
        justify-content: space-between;
        &-name{
            font-family: $font-primary;
            font-weight: 400;
            font-size: 30px;
            line-height: 42px;
            color: #fff;
            padding-top: 63px;
        }
        &-number{
            font-family: $font-primary;
            font-weight: 400;
            font-size: 40px;
            line-height: 56px;
            color: #fff;
            padding: 58px 0;  
            position: relative;
            &::before{
                content: "";
                width: 25px;
                height: 25px;
                background: url(assets/img/tel-icon.png) center no-repeat;
                position: absolute;
                top: 72px;
                left: -50px;
            }   
            &::after{
                content: "";
                width: 100%;
                height: 2px;
                background: #fff;
                position: absolute;
                bottom: 60px;
                left: 0px;
            }             
        }
    }   
}
