.invest-is{
    margin-bottom: 200px;
    .container{
        padding: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 98px;
        color: #3C4D69;
        padding-bottom: 99px;
    }
    .row{
        margin: 0;
    }
    .col-lg-3{
        padding: 0;
    }
    &-block{
        width: 334px;
        height: 534px;
        background: #F6F6F6;
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;
        padding: 40px;
        &-img{
            width: 109px;
            height: 109px;
            margin-bottom: 50px;
            position: relative;
            background-repeat: no-repeat;
            background: url(assets/img/invest-is-sprite.png);
            &.icon-one{
                background-position: 0px 0px;
            }
            &.icon-two{
                background-position: -143px 0px;
            }
            &.icon-three{
                background-position: -286px 0px;
            }
            &.icon-four{
                background-position: -428px 0px;
            }
            &.icon-five{
                background-position: -567px 0px;
            }
            &.icon-six{
                background-position: -709px 0px;
            }
            &.icon-seven{
                background-position: -853px 0px;
            }
        }
        &-title{
            font-family: $font-primary;
            font-weight: 600;
            line-height: 29px;
            color: #3C4D69;
            margin-bottom: 39px;
        }
        &-description{
            font-family: $font-primary;
            font-weight: 400;
            font-size: 18px;
            line-height: 23px;
            color: #707C88;
        }
    }
}