.first-screen {
    width: 100%;
    height: 970px;
    position: relative;
    &::after {
        content: "";
        background: url(assets/img/first-screen-background.svg) center no-repeat;
        position: absolute;
        top: -100px;
        right: 0px;
        width: 100%;
        height: 970px;
        z-index: -2;
    }
    &::before{
        content: "";
        background: url(assets/img/first-screen-before.svg) center no-repeat;
        position: absolute;
        top: -90px;
        right: -448px;
        width: 100%;
        height: 1019px;
        z-index: -1;
    }
    &-title {
        max-width: 820px;
        color: #fff;
        line-height: 72px;
        font-family: $font-primary;
        font-weight: 600;
        padding-top:142px;
        margin-bottom:51px;
    }   
    &-items {
        .first-screen-item { 
            display: flex;
            &-img {
                background-repeat: no-repeat;
                background-image: url(assets/img/sprite-all.png);
                background-position: 0px 0px;
                &.icon-one{
                    width: 45px;
                    height: 47px;
                    background-position: -8px 0px;
                    margin-top: -15px;
                    margin-right: 34px;
                }
                &.icon-two{
                    width: 49px;
                    height: 53px;
                    background-position: -76px 3px;
                    margin-right: 35px;
                }
                &.icon-three{
                    width: 59px;
                    height: 33px;
                    background-position: -150px 0px;
                    margin-right: 27px;
                }
            }
            &-description {
                list-style: none;
                max-width: 615px;
                color: #fff;
                font-size: 20px;
                line-height: 26px;
                font-family: $font-primary;
                font-weight: 400;
                margin-bottom: 37px;
                position: relative;
            }
        }
    }
}

