header{
	height: 100px;
	.navbar {
		.logo-img {
			margin-top: 40px;
		}
		.navbar-nav {
			flex-direction: row;
			margin-top: 47px;
			.nav-item{
				&::before{
				display: none;
				}
				.nav-link {
					font-family: $font-primary;
					font-weight: 500;
					font-size: 16px;
					line-height: 1;	
					color: #fff;
					text-transform: uppercase;
					padding-right: 35px;
					padding-left: 0;
					cursor: pointer;
					z-index: 1;
					&:hover {
						text-decoration: none;
						opacity: .7;
						&:before {
							width: 100%;
						
						}
					}
				}
			}			
		}
		.phone {
			font-family: $font-primary;
			font-weight: 600;
			font-size: 20px;
			line-height: 1;	
			color: #fff;
			margin-top: 44px;
			position: relative;
			&::before {
				content: "";
				position: absolute;
				background: url(assets/img/icon-phone.svg);
				width: 12px;
				height: 12px;
				top: 4px;
				left: -21px;
				
			}
		}		
	}
}