.profit {
    margin-bottom: 200px;
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 91px;
        color: #3C4D69;
        margin-bottom: 60px;
    }
    &-block {
        width: 265px;
        height: 335px;
        padding: 86px 0 0 0;
        margin-right: 92px;
        &-title {
            font-size: 20px;
            line-height: 24px;
            font-family: $font-primary;
            font-weight: 400;
            color: $text-secondary;
            margin-bottom: 19px;
            position: relative;
            &.question:before {
                content: "";
                background:url(assets/img/icon-question.png) center no-repeat;
                position: absolute;
                width: 18px;
                height: 18px;
                top: -8px;
                right: 125px;
            }            
        }   
        &-range-text {
            display: flex;
            justify-content: space-between;
            span {
                font-size: 18px;
                line-height: 21px;
                font-family: $font-primary;
                font-weight: 400;
                color: $text-secondary;
            }          
        }
        &-results {
            width: 357px;
            height: 336px;
            background: #F6F6F6;
            padding: 0;
            &-title {
                font-size: 26px;
                line-height: 36px;
                font-family: $font-primary;
                font-weight: 600;
                color: #3C4D69;
                margin: 60px 50px;
            }
            &-item {
                margin-bottom: 30px;
                span:nth-child(1) {
                    font-size: 20px;
                    line-height: 24px;
                    font-family: $font-primary;
                    font-weight: 400;
                    color: #3C4D69; 
                    margin-left: 30px;
                } 
                span:nth-child(2){
                    font-size: 20px;
                    line-height: 24px;
                    font-family: $font-primary;
                    font-weight: 600;
                    color: #EA142E;
                }   
                .span-one{
                    padding-right: 113px;
                } 
                .span-three{
                    padding-right: 138px;
                }          
            }            
        }
    }
    
}