.characteristics {
    width: 100%;    
    position: relative;
    &::after {
        content: "";
        background: url(assets/img/characteristics-after.svg) center no-repeat;
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 1364px; 
        z-index: -1;
    }
    .container {
        padding: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 84px;
        color: #FFFFFF;
        padding: 191px 255px 76px;
    }    
    &-popovers {         
        position: relative;
        z-index: 1;
        .popover-one {
            position: absolute;
            top: 132px;
            left: 206px;
        }
        .popover-two {
            position: absolute;
            top: 332px;
            left: 362px;
        }
        .popover-three {
            position: absolute;
            top: 112px;
            left: 543px;
        }
        .popover-four {
            position: absolute;
            top: 40px;
            left: 885px;
        }
        .popover-five {
            position: absolute;
            top: 402px;
            left: 921px;
        }
        .popover-six {
            position: absolute;
            top: 208px;
            left: 1148px;
        }
    }
    &-img{
        width: 1351px;
        height: 856px;
    }
}

