.market{
    width: 100%;   
    height: 1104px; 
    position: relative;
    margin-bottom: 188px;
    &::after {
        content: "";
        background: url(assets/img/market-backgraund.png) center no-repeat;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 1104px; 
        z-index: -1;
    }    
    .container{
        padding: 0;
        position: relative;
        &::before{
            content: "";
            width: 886px;
            height: 410px;
            background: url(assets/img/market-img.png) center no-repeat;
            position: absolute;
            bottom: -147px;
            right: 0;
            z-index: 1;
        }
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 80px;
        color: #FFFFFF;
        padding: 199px 0 35px;
        margin: 0;
    }
    &-description{
        font-weight: 400;
        font-size: 26px;
        line-height: 36px;
        color: #8C9BAA;
        padding-bottom: 99px;
        padding-right: 106px;
    }
    &-block{
        display: flex;
        justify-content: space-between;
        margin-bottom: 64px;
        &-item{
            display: flex;
            width: 701px;
            height: 150px;
            color: #FFFFFF;
            font-weight: 500;
            font-size: 20px;
            line-height: 26px;
            border: 1px solid #ebeff014;
            &-img{
                margin: 45px 84px 45px 70px;
                position: relative;
                height: 71px;
                background-repeat: no-repeat;
                background: url(assets/img/sprite-all.png);
                &.icon-one{
                    width: 240px;
                    background-position: 6px -891px;
                }
                &.icon-two{
                    width: 156px;
                    background-position: -191px -891px;
                }
            }
            &-description{
                margin-top: 50px;
                &:last-child{
                    margin-top: 62px;
                }
            }
        }     
    }
    &-percent{
        width: 98px;
        //height: 150px;
        color: #FFFFFF;
        font-weight: 600;
        font-size: 26px;
        line-height: 36px;
        background:#EA142E;
        padding: 5px 17px;
        margin-bottom: 35px;
    }
    &-info{
        width: 454px;
        color: #FFFFFF;
        font-weight: 400;
        font-size: 24px;
        line-height: 33px;
        margin-bottom: 56px;
        & a{
            color: #FFFFFF;
            font-weight: 700;
            text-decoration: underline;
        }
    }
    &-more{
        color: #8C9BAA;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        & a{
            font-weight: 600;
            text-decoration: underline;
        }
    }
}

