.investing{
    margin-bottom: 199px;
    .container {
        padding: 0;
    }
    &-title {
        font-family: $font-primary;
        font-weight: 600;
        line-height: 84px;
        color: #3C4D69;
        padding-right: 846px;
        margin-bottom: 100px;
    }
    &-block{
        display: flex;
        &-item{
            &:first-child{
                margin-right: 101px;
            }
            &-img{
                width: 718px;
                margin-bottom: 50px;
            }
            &-description{
                font-family: $font-primary;
                font-size: 24px;
                font-weight: 600;
                line-height: 29px;
                color: #3C4D69;
                margin-bottom: 47px;
                padding-right: 197px;
            }
            &-example{
                width: 718px;
                border-top: 1px solid #EBEFF0;
                border-bottom: 1px solid #EBEFF0;
                position: relative;
                &::before{
                    content: "";
                    background: url(assets/img/investing-before.png) center no-repeat;
                    width: 40px;
                    height: 33px;
                    position: absolute;
                    top: 32px;
                    left: 0;
                }
                &-title{
                    font-family: $font-primary;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 25px;
                    color: #3C4D69;
                    text-transform: uppercase;
                    margin: 32px 90px 20px;
                }
                &-description{
                    font-family: $font-primary;
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 23px;
                    color: #3C4D69;
                    margin-bottom: 26px;
                    margin-left: 90px;
                    width: 480px;                    
                }
            }
            &-list{
                margin-bottom: 68px;
                width: 612px;
                &-item{
                    font-family: $font-primary;
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 25px;
                    color: #3C4D69;
                    margin-bottom: 31px;
                    //width: 465px;
                    padding-left: 94px;
                    padding-right: 53px;
                    position: relative; 
                    &::before{
                        content: counter(li, decimal) "";
                        position: absolute;
                        top: 9px;
                        left: 0px;
                        color: #fff;
                        font-family: $font-primary;
                        font-size: 30px;
                        font-weight: 400;
                        line-height: 42px;
                        margin: 0 25px;
                        z-index: 1;
                    }   
                    &::after{
                        content: "";
                        width: 64px;
                        height: 64px;
                        background: url(assets/img/investing-list-before.png) center no-repeat;
                        position: absolute;
                        top: 0px;
                        left: 0px;
                    }             
                }
            }
            &-table{
                display: flex;
                margin-bottom: 70px;
                &-colomn{
                    width: 306px;
                    &-title{
                        font-family: $font-primary;
                        font-weight: 600;
                        line-height: 23px;
                        color: #EA142E;
                        text-align: center;
                        margin-bottom: 20px;
                    }
                    &-description{
                        font-family: $font-primary;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 25px;
                        color: #3C4D69;
                        text-align: center;
                        border: 1px solid #EBEFF0;
                        padding: 12px 0;
                        &:nth-child(odd){
                            background: #F6F6F6;
                        }
                    }
                }
            }
        }
    }
}