.liquid-asset{
    .container{
        padding: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 84px;
        color: #3C4D69;
        padding-right: 689px;
        margin-bottom: 99px;
    }
    .row{
        justify-content: space-between;
    }
    &-block{
        width: 457px;
        height: 316px;
        border: 1px solid #EAEAEA;
        padding: 0;
        margin-bottom: 29px;
        &-item{
            display: flex;
            &-img{
                margin: 58px 40px 56px;
                height: 65px;
                background-repeat: no-repeat;
                background-image: url(assets/img/sprite-all.png);
                &.icon-one {
                    width: 120px;
                    background-position: 0px -621px;
                }
                &.icon-two {
                    width: 77px;
                    background-position: -98px -621px;
                }
                &.icon-three {
                    width:120px;
                    background-position: -196px -621px;
                }
                &.icon-four {
                    width: 120px;
                    background-position: -275px -621px;
                }
                &.icon-five {
                    width: 120px;
                    background-position: -366px -621px;
                }
            }
            &-title{
                font-family: $font-primary;
                font-weight: 600;
                line-height: 28px;
                color: #3C4D69;
                margin-top: 62px;
                margin-right: 54px;
            }
        }
        &-description{
            font-family: $font-primary;
            font-weight: 400;
            font-size: 18px;
            line-height: 23px;
            color: #707C88;
            margin:0 20px 0 40px;
        }
    }
    &-photo{
        width: 315px;
        height: 584px;
    }
}