.pension{
    width: 100%;
    position: relative;
    &::after {
        content: "";
        background: url(assets/img/pension-background.png) center no-repeat;
        position: absolute;
        top: 0px;
        right: 0px;
        width: 100%;
        height: 1111px;
        z-index: -1;
    }
    .container{
        padding: 0;
    }
    .row{
        margin: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 98px;
        color: #fff;
        padding: 200px 0 20px;
    }
    &-description{
        font-family: $font-primary;
        font-weight: 600;
        font-size: 26px;
        line-height: 36px;
        color: #fff;
        margin-bottom: 85px;
    }
    &-block{
        width: 699px;
        border: 1px solid #ebeff00f;
        display: flex;
        align-items: center;
        padding: 0 80px;
        position: relative;
        span{
            font-family: $font-primary;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: #fff;
        }
        &:nth-child(1),
        &:nth-child(4){
            height: 173px;
            &::before{
                content: "";
                width: 16px;
                height: 12px;
                background: url(assets/img/pension-check.png)center no-repeat;
                position: absolute;
                top: 80px;
                left: 40px;
                z-index: 1;
            }            
        }
        &:nth-child(4){
            margin-top: 30px;
            &::before{
                content: "";
                width: 16px;
                height: 12px;
                background: url(assets/img/pension-check.png)center no-repeat;
                position: absolute;
                top: 50px;
                left: 40px;
                z-index: 1;                
            }
        }
        &:nth-child(2),
        &:nth-child(3) {
            height: 269px;
            display: flex;
            flex-direction: column;
            position: relative;
            padding-top: 44px;
            span{
               padding-bottom: 20px; 
            }
            &::before{
                content: "";
                width: 16px;
                height: 12px;
                background: url(assets/img/pension-check.png)center no-repeat;
                position: absolute;
                top: 49px;
                left: 40px;
                z-index: 1;
            } 
        }
        &:nth-child(3){
            margin-top: -66px;
        }
        &:nth-child(1),
        &:nth-child(3){
            margin-right:30px;
        }
        &:nth-child(3),
        &:nth-child(4){
            margin-bottom: 200px;
        }
    }
}
