.stable{
    margin: 200px 0;
    .container{
        padding: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 84px;
        color: #3C4D69;
        padding: 0 820px 99px 0;
    }
    &-block{
        display: flex;
        justify-content: space-between;
        &-item{
            width:696px;
            &-title{
                font-family: $font-primary;
                font-weight: 600;
                line-height: 36px;
                color: #3C4D69;
                padding: 43px 0 30px 0;
            }
            &-list,
            &-descroption {
                font-family: $font-primary;
                font-weight: 400;
                font-size: 20px;
                line-height: 26px;
                color: #707C88;
                width: 541px;
            }
            &-list{
                &-item{
                    padding-left: 20px;
                    margin-bottom: 21px;
                    &::before {
                        content: "";
                        background: #707C88;
                    }
                }
            }
        }
    }
}