.why-are-we {
    margin-bottom: 193px;
    .container {
        padding: 0;
    }
    &-title {
        font-family: $font-primary;
        font-weight: 600;
        line-height: 84px;
        color: #3C4D69;
        margin-bottom: 90px;
        width: 843px;
    }
    &-block {
        background: #F6F6F6;
        display: flex;
        &-items {
            width: 503px; 
            padding: 91px 40px 88px 0;  
            &:last-child {
                width: 413px;
                padding: 143px 0 0 0;
                .why-are-we-block-item {
                    padding-bottom: 215px;
                }
            }
            .why-are-we-block-item {
                padding-bottom: 160px;
                display: flex;
                &:last-child {
                    padding-bottom: 0;
                }
                &-description {
                    font-family: $font-primary;
                    font-weight: 500;
                    font-size: 22px;
                    line-height: 28.6px;
                    color: #3C4D69;     
                }
                &-img{
                    position: relative;
                    background-repeat: no-repeat;
                    background-image: url(assets/img/sprite_all.svg);
                    &.hermetically{
                        width: 26px;
                        height: 33px;
                        margin: 0 30px 0 108px;
                        background-position: 0px -368px;
                    }
                    &.сomfortable{
                        width: 47px;
                        height: 36px;
                        margin: 0 21px 0 100px;
                        background-position: -42px -371px;
                    }
                    &.lighting {
                        width: 22px;
                        height: 35px;
                        margin: 10px 30px 0 108px;
                        background-position: -104px -368px;                        
                    }
                    &.profitable {
                        width: 27px;
                        height: 29px;
                        margin: 0 27px 0 107px;
                        background-position: -150px -377px;
                    }
                    &.safely {
                        width: 40px;
                        height: 35px;
                        margin: 0 102px 0 26px;
                        background-position: -194px -373px;                     
                    }     
                    &.access{
                        width: 39px;
                        height: 33px;
                        margin: 0 104px 0 25px;
                        background-position: -243px -373px;
                    }                              
                    &.security {
                        width: 41px;
                        height: 33px;
                        margin: 0 100px 0 21px;
                        background-position: -297px -373px;  
                    }
                }      
                &-description.lighting {
                    color: #EA142E;
                    width: 159px;
                }     
                &-description.safely {
                    margin-left: 140px;
                }
                &-description.access {
                    margin-left: 110px;
                    position: relative;
                    &::after{
                        content: "";
                        background:url(assets/img/icon-question.png) center no-repeat;
                        position: absolute;
                        width: 18px;
                        height: 18px;
                        top: -12px;
                        right: -20px;
                    }
                }
                &-description.security {
                    margin-left: 30px;
                    width: 223px;
                }      
            }                 
        }
        &-img {
            padding-top: 81px;
        }
    }
}

