.comparison-ss{
    margin-bottom: 200px;
    .container {
        padding: 0;
    }
    &-title{
        font-weight: 600;
        line-height: 84px;
        color: #3C4D69;
        padding-bottom: 100px;
        padding-right: 482px;
        margin: 0;
    }
    &-kind{
        font-weight: 600;
        font-size: 24px;
        line-height: 31px;
        color: #fff;
        padding: 7px 19px;
        background-color: #EA142E;
        width: 180px;
        margin-bottom: 46px;
        &:nth-child(4){
            width: 191px;
            margin-bottom: 50px;
        }
    }
    &-table{
        display: flex;
        margin-bottom: 200px;
        &-colomn{
            flex-direction: column;
            &-name{
                font-weight: 500;
                line-height: 31px;
                color: #3C4D69;
                width: 276px;
                margin-bottom: 0; 
                margin-right: 41px; 
                &:first-child{
                    margin-top: 71px;
                    border-top: 1px solid #E3E3E3;     
                    border-bottom: 1px solid #E3E3E3; 
                    padding: 45px 0;    
                }  
                &:nth-child(2){
                    padding: 72px 0;
                    border-bottom: 1px solid #E3E3E3;   
                } 
                &:nth-child(3){
                    padding: 68px 0;
                    border-bottom: 1px solid #E3E3E3;   
                }   
                &:nth-child(4){
                    padding: 82px 0;      
                    border-bottom: 1px solid #E3E3E3;                 
                }       
                &.customer{
                    margin-top: 0;
                    padding: 44px 0;
                }
            }
            &-self-service{
                width: 536px;
                margin-right: 41px;
                color: #3C4D69;
                &-title{
                    font-weight: 600;
                    line-height: 31px;
                    padding: 0 36px 40px;
                    margin: 0;       
                    border-bottom: 1px solid #E3E3E3;   
                    color: #3C4D69;  
                }
                &-item{
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 23px;
                    padding: 37px 90px 37px 36px;
                    &-title{
                        font-weight: 700;
                        margin-bottom: 10px;
                        &:last-child{
                            margin-bottom: 0px;
                        }
                    }
                    &:nth-child(3){
                        padding-bottom: 60px;
                    }
                    &:nth-child(4){
                        padding-bottom: 83px;
                    }
                    &:nth-child(5){
                        padding-bottom: 139px;
                        margin-bottom: 40px;
                    }
                    &:nth-child(3), 
                    &:nth-child(5){
                        background: #F9F9F9;
                    } 
                    &.customer{
                        &:nth-child(1){
                            border-top: 1px solid #E3E3E3;     
                        }
                        &:nth-child(2){
                            background: #F9F9F9;    
                            padding-right: 414px;
                            & b{
                                color: #3C4D69
                            }
                        }                        
                    }                    
                }
            }
            &-warehouse{
                width: 536px;
                color: #3C4D69;
                &-title{
                    font-weight: 600;
                    line-height: 31px;
                    color: #EA142E;
                    padding: 0 36px 40px;
                    margin: 0;     
                    border-bottom: 1px solid #E3E3E3; 
                }
                &-item{
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 23px;
                    padding: 37px 126px 37px 36px;

                    &-title{
                        font-weight: 700;
                        margin-bottom: 10px;
                    }
                    &:nth-child(3){
                        padding-right: 170px;
                    }
                    &:nth-child(4){
                        padding-right: 144px;
                    }
                    &:nth-child(5){
                        margin-bottom: 40px;
                    }
                    &:nth-child(3), 
                    &:nth-child(5){
                        background: #FEF3F5;
                    }   
                    &.customer{
                        &:nth-child(1){
                            border-top: 1px solid #E3E3E3;     
                        }
                        &:nth-child(2){
                            background: #FEF3F5;   
                            padding-right: 414px;
                        }                        
                    }  
                }
            } 
        }
    }
}