.we-are-doing {
    margin-bottom: 184px;
    .container {
        display: flex;
    }
    &-img {
        width: 701px;
        height: 647px;
        margin-right: 120px;
    }
    &-info {
        width: 534px;
        margin-top: 105px;
            &-title {
            font-family: $font-primary;
            font-weight: 600;
            line-height: 84px;
            color: #3C4D69;
            margin-bottom: 42px;
        }
        &-items {
            font-family: $font-secondary;
            font-size: 20px;
            font-weight: 400;
            line-height: 26px;
            color: #3C4D69;                   
            .we-are-doing-info-item {
                margin-bottom: 30px;
                padding-left: 20px;
            }
        }
    }
}