footer{
  width: 100%;
  position: relative;
  &::after {
    content: "";
    background: url(assets/img/footer-background.png) center no-repeat;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 701px; 
    z-index: -1;
  }
  .container{
    padding: 0;
  }
  .footer-date{
    display: flex;
    color: #FFFFFF;
    font-family: $font-primary;
    border-bottom: 1px solid #ffffff0d;
    &-contacts{
      margin-top: 106px;
      &-item{
        &-tell{
          font-weight: 400;
          font-size: 32px;
          line-height: 44px;
          color: #fff;
          margin-right: 139px;
          margin-bottom: 7px;
        }
        &-media{
          a{
            margin-right: 37px;
          }
        }
      }
    }
    &-clients, &-cooperation{
      width: 168px;
      font-weight: 500;
      font-size: 22px;
      line-height: 30px;
      margin: 121px 120px 0 0;
      position: relative;
      span{
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
        color: #8C9BAA;
      }
      &::before{
        content: "";
        width: 23px;
        height: 23px;
        background: url(assets/img/footer-icon-email.png) center no-repeat;
        position: absolute;
        top: 20px;
        left: -56px;
      }
      &::after{
        content: "";
        width: 60px;
        height: 60px;
        position: absolute;
        top: 0px;
        left: -74px;
        border: 1px solid #ffffff1a;
        border-radius: 50%;
      }
    }
    &-cooperation{
      position: relative;
      &::before{
        content: "";
        width: 23px;
        height: 23px;
        background: url(assets/img/footer-icon-email.png) center no-repeat;
        position: absolute;
        top: 20px;
        left: -56px;
      }
      &::after{
        content: "";
        width: 60px;
        height: 60px;
        position: absolute;
        top: 0px;
        left: -74px;
        border: 1px solid #ffffff1a;
        border-radius: 50%;
      }
    }
    &-address-one, &-address-two{
      width: 175px;
      font-weight: 400;
      font-size: 18px;
      line-height: 32px;
      margin: 100px 120px 0 0;
      position: relative;
      &::before{
        content: "";
        width: 20px;
        height: 25px;
        background: url(assets/img/footer-icon.png) center no-repeat;
        position: absolute;
        top: 35px;
        left: -58px;
      }
      &::after{
        content: "";
        width: 60px;
        height: 60px;
        position: absolute;
        top: 15px;
        left: -78px;
        border: 1px solid #ffffff1a;
        border-radius: 50%;
      }
    }
    &-address-two{
      margin-right: 0;
      margin-bottom: 100px;
      position: relative;
      &::before{
        content: "";
        width: 20px;
        height: 25px;
        background: url(assets/img/footer-icon.png) center no-repeat;
        position: absolute;
        top: 35px;
        left: -58px;
      }
      &::after{
        content: "";
        width: 60px;
        height: 60px;
        position: absolute;
        top: 15px;
        left: -78px;
        border: 1px solid #ffffff1a;
        border-radius: 50%;
      }
    }
  }  
  .footer-list{  
    display: flex; 
    font-family: $font-primary; 
    &-column{
      display: flex;
      flex-direction: column;
      margin-top: 32px;
      span{
        color: #FFFFFF;
        font-weight: 400;
        font-size: 18px;
        line-height: 54px;
        margin-right: 102px;
      }
    }
    &-requisites{
      margin-top: 47px;
      &-title{
        color: #FFFFFF;
        font-weight:600;
        font-size: 24px;
        line-height: 33px;  
        margin-bottom: 30px;  
      }
      &-row {
        display: flex;
        &-column{
          display: flex;
          flex-direction: column;
          color: #8C9BAA;
          font-weight:600;
          font-size: 18px;
          line-height: 16px; 
          margin-right: 53px;
          margin-bottom: 77px;
          span{
            margin-bottom: 25px;
            width: 270px;
          }
          &:last-child{
            margin-right: 0;
          }
        }
      }        
    }
  }
  .footer-end{
    font-family: $font-primary; 
    color: #8C9BAA;
    font-weight:400;
    font-size: 18px;
    line-height: 22px; 
    display: flex;
    justify-content: center;
    span{
      padding-left: 25px;
    }
  }
}

 