.application{
    margin-bottom: 187px;
    .container{
        padding: 0;
        background: #F6F6F6;
    }
    &-block{
        display: flex;
        &-info{
            padding: 94px 84px 0 88px;

            &-title{
                font-family: $font-primary;
                font-weight: 600;
                line-height: 60px;
                color: #3C4D69;
                margin-bottom: 39px;
            }
            &-description{
                font-family: $font-primary;
                font-weight: 400;
                font-size: 26px;
                line-height: 36px;
                color: #3C4D69;
            }            
        }
        &-form{
            padding: 100px 90px 100px 0;
            &-info{
                display: flex;
                margin-bottom: 12px;
                input{
                    width: 290px;
                    padding: 27px 40px;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 22px;
                    color: #707C88;
                    margin-right: 12px;
                    border: none;
                    &:last-child{
                        margin-right: 0;
                    }
                    &:focus-visible{
                        outline: none;
                    }
                }
            }
            textarea{
                width: 894px;
                height: 212px;
                margin-bottom: 45px;
                padding: 27px 40px;
                font-weight: 400;
                font-size: 16px;
                line-height: 22px;
                color: #707C88;
                border: none;
                &:focus-visible{
                    outline: none;
                }
            }
            .btn{
                width: 894px;
                height: 88px;
            }            
        }
    }
}
