.development{
    width: 100%;    
    position: relative;
    margin-bottom: 200px;
    &::after {
        content: "";
        background: url(assets/img/development-backgraund-after.png) center no-repeat;
        position: absolute;
        bottom: 0px;
        left: 0;
        width: 100%;
        height: 167px;
        z-index: -1;
    }
    .container{
        padding: 0;
    }
    &-title{
        font-family: $font-primary;
        font-weight: 600;
        line-height: 60px;
        color: #3C4D69;
        margin-bottom: 39px;
    }
    &-info{
        display: flex;
        &-items{
            margin-right: 606px;
            .development-info-item{
                font-family: $font-primary;
                font-weight: 500;
                font-size: 18px;
                line-height: 23px;
                color: #3C4D69;
                margin-bottom: 20px;
                margin-left: 24px;
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    width: 12px;
                    height: 12px;
                    background: #3C4D69;
                    top: 3px;
                    left: -24px;
                }
                &:last-child{
                    color: #EA142E; 
                    &::before{
                        background: #EA142E;
                    }
                }
            }
        }
        &-block{
            width: 380px;
            background: #EA142E;
            color: #fff;
            font-family: $font-primary;
            font-weight: 600;
            font-size: 22px;
            line-height: 28px;
            padding: 34px 39px;
            margin-top: -78px;
            margin-bottom: 45px;
        }
        
    }
    .graph-and-materials{
        display: flex;
        .graph {
            #q-graph {
                display: block; 
                position: relative; 
                width: 865px; 
                height: 550px;
                margin: 89px 0 113px 58px;
                tr, th, td {
                    position: absolute;
                    bottom: 0; 
                    width: 35px; 
                    z-index: 2;
                    //margin: 0; 
                    //padding: 0;
                    text-align: center;
                } 
                tbody {
                    tr {
                        height: 286px;
                        //padding-top: 2px;
                        //color: #3C4D69;
                    }
                    th {
                        bottom: -25px; 
                        left: -5px;
                        vertical-align: top;
                        font-family: $font-primary;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 17px;
                        color: #707C88;
                    }
                }
                #q1 {
                    left: 44px;
                }
                #q2 {
                    left: 152px;
                }
                #q3 {
                    left: 260px;
                }
                #q4 {
                    left: 368px; 
                }
                #q5 {
                    left: 476px;
                }
                #q6 {
                    left: 585px;
                }
                #q7 {
                    left: 693px;
                }
                #q8 {
                    left: 800px;
                }
                .bar {
                    width: 20px; 
                    color: #3C4D69;
                    p {
                        width: 46px;
                        font-family: $font-primary;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 20px;
                        margin-top: -30px;
                        margin-left: -11px;
                        color: #3C4D69;
                    }
                }
                .sent {
                    background-color: #3C4D69;
                }  
                .item{
                    p {
                        font-family: $font-primary;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 20px;
                        color: #EA142E;
                        width: 78px;
                        border-bottom: 3px solid #EA142E;
                        margin-bottom: 405px;
                        margin-left: -27px;
                    }
                }                 
            }  
            #ticks {
                position: relative; 
                top: -663px; 
                left: 59px;
                width: 923px; 
                height: 550px; 
                z-index: 1;
                margin-bottom: -503px;
                .tick {
                    position: relative; 
                    border: 1px solid #EDEDED;
                    width: 865px; 
                    p {
                        position: absolute; 
                        left: -54px; 
                        top: -12px; 
                        font-family: $font-primary;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 17px;
                        color: #707C88;
                    }
                }
            }
            #percents {
                position: relative; 
                top: -720px; 
                right: -45px;            
                width: 923px; 
                height: 550px; 
                z-index: 1;
                margin-bottom: -560px;
                .percent {
                    position: relative; 
                    p {
                        position: absolute; 
                        right: 0px;
                        top: 0px; 
                        font-family: $font-primary;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 17px;
                        color: #707C88;
                    }
                }
            }
        }  

        .materials{
            &-title{
                color: #3C4D69;
                font-family: $font-primary;
                font-weight: 600;
                line-height: 60px;
                margin-left: 144px;
                margin-bottom: 35px;
            }
            &-item{
                width: 380px;
                height: 60px;
                position: relative;
                margin: 0 0 10px 127px;
                border: 1px solid #EDEDED;
                padding-top: 19px;
                &::before{
                    content: "";
                    width: 60px;
                    height: 60px;
                    background: url(assets/img/icon-pdf.png) center no-repeat;
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
                a{
                    font-family: $font-primary;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 23px;
                    color: #3C4D69;
                    padding-left: 90px;
                    &:hover{
                        text-decoration: none;
                        color: #707C88;
                    }
                }                
            }
        }   
    }    
    &-stock{
        display: flex;
        justify-content: space-between;
        margin-bottom: 200px;
        &-data{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-family: $font-primary;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #3C4D69;
            margin-left: 26px;
            span{
                position: relative;
                &::before{
                    content: "";
                    position: absolute;
                    width: 12px;
                    height: 12px;
                    background: #F6F6F6;
                    top: 3px;
                    left: -26px;
                }
                &:nth-child(2)::before{
                    background: #8E669B;
                }
                &:nth-child(3)::before{
                    background:#9CA6B5;
                }
                &:nth-child(4)::before{
                    background: #616C7C;
                }
                &:nth-child(5)::before{
                    background: #E2DBD3;
                }
                &:nth-child(6)::before{
                    background: #8DACD8;
                }
            }                       
        }       
        &-characteristic{
            display: flex;
            &-item{
                font-family: $font-primary;
                font-weight: 600;
                font-size: 16px;
                line-height: 19px;
                color: #3C4D69;
                background: #F6F6F6;
                padding: 128px 14px 131px;
                &:nth-child(2){
                    background: #8E669B;
                    color: #fff;
                    padding: 128px 135px 131px;
                }
                &:nth-child(3){
                    background:#9CA6B5;
                    color: #fff;
                    padding: 128px 148px 131px;
                }
                &:nth-child(4){
                    background: #616C7C;
                    color: #fff;
                    padding: 128px 161px 131px;
                }
                &:nth-child(5){
                    background: #E2DBD3;
                    padding: 128px 74px 131px;
                }
                &:nth-child(6){
                    background: #8DACD8;
                    color: #fff;
                    padding: 128px 13px 131px;
                }
            }
        }
    }
    .join-block{
        font-family: $font-primary;
        font-weight: 500;
        font-size: 30px;
        line-height: 42px;
        color: #fff;
        padding: 62px 170px;
        position: relative;
        span{
            margin-right: 110px;
        }
        &::after{
            content: "";
            position: absolute;
            width: 78px;
            height: 62px;
            background: url(assets/img/development-after.png) center no-repeat;
            top: 58px;
            left: 550px;
            z-index: 1;
        }
    }
}