.dynamics {
    margin-bottom: 92px;
    &-title {
        font-family: $font-primary;
        font-weight: 600;
        line-height: 91px;
        color: #3C4D69;
        margin-bottom: 88px;
    }
    &-block {
        border: 1px solid #EBEFF0;
        display: flex;
        margin-bottom: 30px;
        height: 309px;
        &.next {
            background: #3C4D69;
            color: #fff;
        }
        &-img {
            background-repeat: no-repeat;
            background-image: url(assets/img/sprite_all.svg);

            &.icon-one {
                width: 113px;
                height: 76px;
                margin: 120px 164px;
                background-position: 0px -107px;
            }
            &.icon-two {
                width: 232px;
                height: 159px;
                margin: 79px 105px;
                background-position: -137px -107px;
            }
            &.icon-three {
                width: 185px;
                height: 194px;
                margin: 62px 129px;
                background-position: -390px -107px;
            }
            &.icon-four {
                width: 280px;
                height: 169px;
                margin: 75px 102px;
                background-position: -596px -107px;
            }
        }
        &-info {
            font-family: $font-primary;
            margin-top: 64px;
            &-date {
                width: 114px;
                font-size: 24px;
                font-weight: 600;
                line-height: 33.6px;
                color: #fff;
                background: #EA142E;
                padding: 2px 9px;
                margin-bottom: 31px;
                &.next {
                    color: #3C4D69;
                    background: #fff;
                }
            }
            &-items {
                font-size: 20px;
                font-weight: 400;
                line-height: 28px;
                color: #3C4D69;
                padding-left: 20px;                
                .dynamics-block-info-item {
                   padding-bottom: 10px; 
                   padding-left: 0px;
                   &::before {
                    content: "";
                    margin-left: -20px;
                    background-color: #3C4D69;
                    }
                    &.next {
                        &::before {
                            content: "";
                            background-color: #fff;
                        }
                    }
                }
                &.next {
                    color: #fff;
                }
            }
            &-coordinates {
                font-size: 18px;
                font-weight: 400;
                line-height: 25.2px;
                color: #707C88;
                padding-left: 22px;
                margin-bottom: 57px;
                position: relative;
                
                &::before {
                    content: "";
                    width: 12px;
                    height: 17px;
                    background: url(assets/img/icon-coordinates.svg) center no-repeat;
                    position: absolute;
                    top: 3px;
                    left: 0px;
                }
                &.next{
                    width: 167px;
                    margin-right: 77px;
                }
            }
        }
    }
}

